import React from "react";
import "./language.css";
export default function Language() {
  const value = [
    {
      text: "Python",
      percentage: 87
    },
    {
      text: "Java",
      percentage: 60
    },
    {
      text: "Kotlin",
      percentage: 40
    },
    {
      text: "VB.Net",
      percentage: 60
    },
    {
      text: "CSS",
      percentage: 70
    },
    {
      text: "HTML",
      percentage: 70
    },
    {
      text: "JavaScript",
      percentage: 60
    },
    {
      text: "C",
      percentage: 65
    },
    {
      text: "ReactJS",
      percentage: 50
    },
    {
      text: "Android Development",
      percentage: 63
    }
  ];
  return (
    <>
      <div>
        <div className="row">
          {value.map((value, index) => {
            return (
              <>
                <div className="col-lg-6 col-mg-6 col-sm-12 my-2">
                  <span className="language">{value.text}</span>
                  <div className="progress-some">
                    <div
                      className="progress-new"
                      style={{ width: `${value.percentage}%` }}
                    ></div>
                  </div>
                </div>
              </>
            );
          })}
        </div>
      </div>
    </>
  );
}
