import React, { useState, useRef } from "react";
import emailjs from "@emailjs/browser";
import "./contact.css";
export default function Contact() {
  const [name, setname] = useState("");
  const [email, setmail] = useState("");
  const form = useRef();
  const [message, setmessage] = useState("");
  const sendEmail = (e) => {
    e.preventDefault();
    console.log(form.current);
    const templateparams = {
      from_name: name + " " + email,
      to_name: "Alpa Meherkar",
      message: message
    };

    emailjs
      .send(
        "service_iv1y9kq",
        "template_1f7y7vk",
        templateparams,
        "Ctue9sk2du46OJ1KX"
      )
      .then(
        function (response) {
          console.log("SUCCESS!", response.status, response.text);
        },
        function (error) {
          console.log("FAILED...", error);
        }
      );
  };
  return (
    <>
      <div>
        <div className="contact-me-card row">
          <div className="col-lg-6 col-md-5 col-sm-12 left-contact px-2 py-2">
            <span className="get-in-touch mx-4 my-5">Get in Touch</span>
            <div className="py-5 d-flex justify-content-center">
              <lottie-player
                className="lottie2"
                src="https://assets1.lottiefiles.com/packages/lf20_in4cufsz.json"
                background="transparent"
                speed="1"
                loop
                autoplay
              ></lottie-player>
            </div>
          </div>
          <div className="col-lg-6 col-md-5 col-sm-12 my-5">
            <form
              ref={form}
              className="d-flex flex-column card-contact-right"
              onSubmit={sendEmail}
            >
              <div className="input-group my-3 d-flex flex-column">
                <label> Name </label>
                <input
                  value={name}
                  onChange={(e) => {
                    setname(e.target.value);
                  }}
                  type="text"
                  placeholder="Enter your name"
                  className="input-groups"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <label> Email </label>
                <input
                  value={email}
                  onChange={(e) => {
                    setmail(e.target.value);
                  }}
                  type="text"
                  placeholder="Enter your email"
                  className="input-groups"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <label> Message </label>
                <textarea
                  value={message}
                  onChange={(e) => {
                    setmessage(e.target.value);
                  }}
                  type="text"
                  placeholder="Enter a message"
                  className="input-groups"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <input
                  className="btn btn-sucess"
                  type="submit"
                  value="Send Message"
                />
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
