import React from "react";
import "./testimonials.css";
export default function Testimonial() {
  const testimonials = [
    {
      name: "- Ryan",
      text:
        "This person is an awesome person. Knows how to blend with new knowledge."
    },
    { name: "- Leona", text: "She is excellent and honest." },
    {
      name: "- Nate",
      text: "Very keen to learn new skills. I am proud of her."
    }
  ];
  return (
    <>
      <div>
        <div className="row mx-5">
          {testimonials.map((value) => {
            return (
              <div className="col-lg-4 col-sm-12 col-md-6 my-3">
                <div className="card shadow testimonial-card d-flex flex-column">
                  <span className="descp">{value.text}</span>
                  <span className="stars">⭐⭐⭐⭐</span>
                  <span className="name">{value.name}</span>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
}
